/** @jsx jsx */
/** @jsxFrag React.Fragment */
import { Box, jsx } from "@reflex/ui/src";
import { useMenus } from "docz";
import { useState } from "react";
import { NavGroup } from "../NavGroup";
import { NavLink } from "../NavLink";
import { NavSearch } from "../NavSearch";

export const Sidebar = ({ open }) => {
  const [query, setQuery] = useState("");
  const menus = useMenus({ query });
  const handleChange = (ev) => {
    setQuery(ev.target.value);
  };

  return (
    <div
      css={{
        minHeight: "calc(100vh - 67px)",
        width: 250,
        marginLeft: open ? 0 : -250,
        overflow: "hidden",
        transition: "all .2s cubic-bezier(0,.3,.6,1)",
        height: "100%",
      }}
    >
      <Box
        bgColor="paper"
        css={{
          borderRight: "1px solid rgba(0,0,0,.2)",
          minHeight: "calc(100vh - 67px)",
          height: "100%",
          marginLeft: open ? 0 : -250,
        }}
        p={[24, 4]}
      >
        <NavSearch
          placeholder="Type to search..."
          value={query}
          onChange={handleChange}
        />
        <div css={{ minHeight: "100%", overflow: "hidden overlay" }}>
          {menus.map((menu, k) => {
            if (!menu.route) return <NavGroup key={menu.id + k} item={menu} />;
            // if (menu.route === currentDoc.route) {
            //   return (
            //     <NavLink key={menu.id} item={menu}>
            //       {menu.name}
            //     </NavLink>
            //   );
            // }
            return (
              <NavLink key={menu.id + k} item={menu}>
                {menu.name}
              </NavLink>
            );
          })}
        </div>
      </Box>
    </div>
  );
};
