/** @jsx jsx */
import { Box, Collapse, css, IconButton, jsx } from "@reflex/ui/src";
import copy from "copy-text-to-clipboard";
import { useConfig } from "docz";
import Highlight, { defaultProps } from "prism-react-renderer";
import theme from "prism-react-renderer/themes/github";
import React from "react";
import { LivePreview } from "react-live";
import { usePrismTheme } from "~utils/theme";
import * as Icons from "../Icons";
import * as styles from "./styles";
import { Wrapper } from "./Wrapper";

const PrismHighlighter = ({ code, language = "jsx" }) => (
  <Highlight {...defaultProps} theme={theme} code={code} language={language}>
    {({ className, style, tokens, getLineProps, getTokenProps }) => (
      <pre className={className} style={style}>
        {tokens.map((line, i) => (
          <div {...getLineProps({ line, key: i })}>
            {line.map((token, key) => (
              <span {...getTokenProps({ token, key })} />
            ))}
          </div>
        ))}
      </pre>
    )}
  </Highlight>
);

export const Playground = ({
  code,
  scope,
  language,
  useScoping = false,
  component,
}) => {
  const {
    themeConfig: { showPlaygroundEditor, showLiveError, showLivePreview },
  } = useConfig();
  // Makes sure scope is only given on mount to avoid infinite re-render on hot reloads
  const theme = usePrismTheme();
  const [showingCode, setShowingCode] = React.useState(showPlaygroundEditor);
  const copyCode = () => copy(code);
  const toggleCode = () => setShowingCode((s) => !s);
  return (
    <Box p={0} m={[32, 0]} bgColor="paper" radius={4} shadow={1}>
      <Box
        p={[24, 24]}
        radius={8}
        m={[16, 0]}
        css={css`
          > * {
            margin: 8px;
          }
        `}
      >
        {component}
      </Box>

      <Wrapper
        content="preview"
        useScoping={useScoping}
        showingCode={showingCode}
      >
        <Collapse open={showLivePreview}>
          <LivePreview css={styles.preview} data-testid="live-preview" />
        </Collapse>
      </Wrapper>

      <div css={styles.buttons}>
        <IconButton
          css={styles.button}
          onClick={copyCode}
          color="#454545"
          icon={<Icons.Clipboard size={16} />}
        />
        <IconButton
          css={styles.button}
          onClick={toggleCode}
          color="#454545"
          icon={<Icons.Code size={16} />}
        />
      </div>
      <Collapse open={showingCode}>
        <Box bgColor="transparent" m={[4, 0, 0, 0]}>
          <div css={styles.editor(theme)}>
            <PrismHighlighter code={code} language={language} />
          </div>
        </Box>
      </Collapse>
    </Box>
  );
};
