/** @jsx jsx */
import { jsx, Box, IconButton, Button } from "@reflex/ui/src";
import { useConfig, useCurrentDoc } from "docz";

import * as styles from "./styles";
import { Edit, Menu, Sun, Github } from "../Icons";
import { Logo } from "../Logo";

export const Header = (props) => {
  const { onOpen } = props;
  const {
    repository,
    themeConfig: { showDarkModeSwitch, showMarkdownEditButton },
  } = useConfig();
  const { edit = true, ...doc } = useCurrentDoc();
  // const [colorMode, setColorMode] = useColorMode()
  const colorMode = "light";
  const toggleColorMode = () => {
    // setColorMode(colorMode === 'light' ? 'dark' : 'light')
  };

  return (
    <Box
      bgColor="primary"
      p={[12, 24]}
      display="flex"
      css={{ alignItems: "center" }}
      data-testid="header"
    >
      <div>
        <IconButton onClick={onOpen} color="white" size={32} icon={<Menu />} />
      </div>
      <Logo />
      <Box>
        {repository && (
          <Box mr={2}>
            <IconButton icon={<Github />} color="white" />
          </Box>
        )}
        {showDarkModeSwitch && (
          <IconButton
            onClick={toggleColorMode}
            aria-label={`Switch to ${colorMode} mode`}
            icon={<Sun size={15} />}
          />
        )}
      </Box>
      {showMarkdownEditButton && edit && doc.link && (
        <Button
          css={styles.editButton}
          // href={doc.link}
        >
          <Edit width={14} />
          <Box sx={{ pl: 2 }}>Edit page</Box>
        </Button>
      )}
    </Box>
  );
};
