import { useTheme } from "@reflex/ui/src";
import { get, pipe, defaultTo } from "lodash/fp";

export const themeProp = (str) => (props) => {
  return get(`theme.${str}`, props);
};

export const usePrismTheme = () => {
  const theme = useTheme();
  const getTheme = pipe(
    get("prismTheme"),
    // defaultTo(get(`prism.${theme.palette.type}`, theme))
    defaultTo(get(`prism.dark`, theme))
  );
  return getTheme(theme);
};
